import React, { useState } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { theme } from '../themes/theme';
import Menu from './Menu';
import useScreenWidth from '../hooks/ScaleUtils';

const Header = ({ scrollTo, refs }) => {
  const [menuOpen, setMenuOpen] = useState(false); // State to toggle menu visibility
  const { screenWidth, scaleMenuFont } = useScreenWidth();
  const fontSize = 16;
  const navItem = screenWidth < 700 ? { marginRight: 12} : { marginRight: 40}; 

  const toggleMenu = () => {
    setMenuOpen(!menuOpen); // Toggle menu visibility
  };

  return (
    <View ref={refs.home} style={styles.header}>
      <View style={[styles.nav, { display: menuOpen ? 'flex' : 'none' }]}>
        <View style={navItem}>
          <Text style={[styles.navText, {fontSize: scaleMenuFont(fontSize, screenWidth) }]} onPress={() => scrollTo(refs.home)}>
            Home
          </Text>
        </View>
        <View style={navItem}>
          <Text style={[styles.navText, {fontSize: scaleMenuFont(fontSize, screenWidth) }]} onPress={() => scrollTo(refs.graphic)}>
            Services
          </Text>
        </View>
        <View style={navItem}>
          <Text style={[styles.navText, {fontSize: scaleMenuFont(fontSize, screenWidth) }]} onPress={() => scrollTo(refs.contact, 'contact')}>
            Contact
          </Text>
        </View>
      </View>
      <Menu toggleMenu={toggleMenu} menuOpen={menuOpen} /> {/* Add hamburger menu */}
    </View>
  );
};

const styles = StyleSheet.create({
  header: {
    display:'flex',
    flexDirection: 'row',
    height:'auto',
    justifyContent: 'right',
    alignItems: 'center',
    backgroundColor: theme.colors.secondBackground,
    padding: 40,
  },
  nav: {
    flexDirection: 'row',
  },
  navText: {
    fontWeight: 'bold',
    fontFamily: 'Circular Std',
    color: theme.colors.secondText,
    cursor: 'pointer', // Adds a pointer cursor to indicate clickable text
  },
});

export default Header;
